@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  margin: 0;
  background-size: 100% 100%;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-repeat: no-repeat;
}

@-webkit-keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  font-family: "Baloo 2";
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: linear-gradient(
    -70deg,
    rgb(165, 70, 87),
    rgb(159, 126, 105),
    rgb(21, 122, 110),
    rgb(61, 64, 91)
  );
  background-repeat: no-repeat;
  background-size: cover;
}

.page-wrapper {
  background: linear-gradient(
    -70deg,
    rgb(165, 70, 87),
    rgb(159, 126, 105),
    rgb(21, 122, 110),
    rgb(61, 64, 91)
  );
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  -webkit-animation: gradient 15s ease infinite;
          animation: gradient 15s ease infinite;
}

div {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -o-user-select: none;
}

button {
  background-color: rgb(129, 178, 154);

  border: none;
  color: rgb(250, 229, 205);
  padding: 10px;
  text-align: center;
  text-decoration: none;
  font-weight: 550;
  font-size: 16px;
  margin: 4px 2px;
  border-radius: 4px;
  min-width: 100px;
  box-shadow: 0 5px 4px 0 rgba(0, 0, 0, 0.2);

  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
button:active {
  background-color: rgb(85, 122, 103);
}
button:active {
  background-color: rgb(104, 179, 142);
  box-shadow: 0 5px #666;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

button:disabled,
button[disabled] {
  background-color: rgb(61, 64, 91);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.connect {
  background-size: auto;
}

.screen {
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
}
/*  Used for providing space between components
 */
.spacerXSmall {
  height: 8px;
  width: 8px;
}

/*  Used for providing space between components */
.spacerSmall {
  height: 16px;
  width: 16px;
}

/* Used for providing space between components */
.spacerMedium {
  height: 24px;
  width: 24px;
}

/*  Used for providing space between components */
.spacerLarge {
  height: 32px;
  width: 32px;
}

/* Used for providing a wrapper around a component */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
}

.row {
  flex-direction: row;
}

.column {
  flex-direction: column;
}

.textTitle {
  color: var(--white);
  font-size: 20px;
  font-weight: 500;
}

.textSubTitle {
  color: var(--white);
  font-size: 16px;
  font-weight: 500;
}

.textDescription {
  color: var(--white);
  font-size: 14px;
  font-weight: 600;
  padding: 2px;
}

.styledClickable :active {
  opacity: 0.6;
}

.mint {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.connect {
  margin-top: 6rem;
}

.card {
  box-shadow: 0 10px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  min-width: 200px;
  background: rgb(250, 229, 205);
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  margin: 20px;
  padding: 20px;
}

.card:hover {
  box-shadow: 0 20px 16px 0 rgba(0, 0, 0, 0.2);
}

.card-data {
  margin-top: 10px;
  color: rgb(102, 102, 102);
  font-family: san serif;
}
.item-value {
  color: rgb(61, 64, 91);
  font-weight: 1000;
  margin-left: 10px;
  font-family: serif;
}

#frame1 {
  width: 100%;
  background-color: transparent;
}

.nft-container {
  margin: 20px;
}

.who-am-i {
  margin: 20px;
  color: rgb(107, 110, 138);
  font: 700 normal 1.5em "Inconsolata";
  text-shadow: 2px 2px rgba(87, 87, 87, 0.3);
  position: absolute;
}

:root {
  --yellow: #fccd35;
  --pink: #ff19e9;
  --green: #00cc00;
  --white: #ffffff;
  --light-grey: #b1b1b1;
  --dark-grey: #2b2b2b;
  --black: #000000;
}

